*{margin:0;padding:0;box-sizing:border-box}body{background:#020024;background:linear-gradient(25deg, rgb(2, 0, 36) 1%, rgb(19, 70, 120) 41%, rgb(61, 182, 207) 100%);color:#fff;font-family:"Poppins",sans-serif;min-height:100vh}header{font-size:2rem}header,form{min-height:20vh;display:flex;justify-content:center;align-items:center}form input,form button{padding:.8rem;font-size:1.8rem;border:none;background:#fff}form input{border-top-left-radius:8px;border-bottom-left-radius:8px}form button{color:#020024;background:#f7fffe;border-top-right-radius:8px;border-bottom-right-radius:8px;cursor:pointer;transition:all .3s ease}form button:hover{background:#020024;color:#fff}.todo-container{display:flex;justify-content:center;align-items:center}.todo-list{min-width:30%;list-style:none}.todo{margin:.5rem;background:#fff;font-size:1.5rem;color:#000;display:flex;justify-content:space-between;align-items:center;transition:all 1s ease}.filter-todo{padding:1rem}.todo li{flex:1}.trash-btn,.complete-btn{background:#020024;color:#fff;border:none;padding:1rem;cursor:pointer;font-size:1rem}.complete-btn{background:#0bd4a2}.todo-item{padding:0rem .5rem}.fa-trash,.fa-check{pointer-events:none}.fall{transform:translateY(10rem) rotateZ(20deg);opacity:0}.completed{text-decoration:line-through;opacity:.5}select{-webkit-appearance:none;-moz-appearance:none;-ms-appearance:none;appearance:none;outline:0;box-shadow:none;border:0 !important;background-image:none}.select{margin:1rem;position:relative;overflow:hidden;border-radius:8px}select{color:#134678;font-family:"Poppins",sans-serif;cursor:pointer;width:12rem}.select::after{content:"â–¼";position:absolute;top:0;right:0;padding:1rem;background:#020024;cursor:pointer;pointer-events:none;transition:.4s ease;border-top-right-radius:8px;border-bottom-right-radius:8px}.select:hover{color:#62cfe6}